
import Divider from 'vux/src/components/divider/index.vue'

import VHeader from '@/components/v-header/v-header'
import axios from '@/api/axiosApi'
import { isIOS } from '@/assets/js/brower'
import { localUser } from '@/assets/js/local'
import md5 from 'js-md5'

let countDown = 60
export default {
data () {
return {
clickBack: false,
title: '找回密码(验证手机号)',
phone: '',
validateCode: '',
validateName: '获取验证码',
password: '',
rePassword: '',
submitPhone: false
}
},
components: {
VHeader, Divider
},
created () {},
methods: {
beforeLeave (el) {

if (isIOS() && !this.clickBack) {
el.style.display = 'none'
}
},
setTimeDown () {
if (countDown === 0) {
this.validateDisabled = false
this.validateName = '重新获取'
countDown = 60
return
} else {
this.validateDisabled = true
this.validateName = '重新发送(' + countDown + ')'
countDown--
}
const _self = this
setTimeout(() => {
_self.setTimeDown()
}, 1000)
},
getValidate () {
if (this.validateDisabled) {
return
}
if (!(/^1[34578]\d{9}$/.test(this.phone))) {
this.$vux.toast.text('手机号码格式不正确', 'middle')
return
}
if (countDown >= 60) {
const _self = this
axios.get(_self, `/v1/sms/${this.phone}`, null, (data) => {
_self.$vux.toast.show('发送验证码成功', 'middle')
})
}
this.setTimeDown()
},
blurPassword (event) {
if (!(/^([\d]|[\w]){6,16}$/.test(this.password))) {
this.$vux.toast.text('密码长度为6到16位并且只包含数字字母下划线', 'middle')
}
},
confirmPhone () {
if (this.phone === '') {
this.$vux.toast.text('手机号码不能为空', 'middle')
return
}
if (!(/^1[34578]\d{9}$/.test(this.phone))) {
this.$vux.toast.text('手机号码格式不正确', 'middle')
return
}
if (this.validateCode.length !== 6) {
this.$vux.toast.text('验证码格式不正确', 'middle')
return
}
const _self = this
axios.post(_self, `/v1/sms/${this.phone}/${this.validateCode}`, null, (data) => {
_self.submitPhone = true
_self.title = '重置密码'
})
},
confirmPassword () {

if (this.password === '' || this.rePassword === '') {
this.$vux.toast.text('请输入密码再提交', 'middle')
return
}
if (this.password !== this.rePassword) {
this.$vux.toast.text('两次输入的密码不一致', 'middle')
return
}
if (!(/^([\d]|[\w]){6,16}$/.test(this.password))) {
this.$vux.toast.text('密码长度为6到16位并且只包含数字字母下划线', 'middle')
return
}
const _self = this
axios.post(_self, '/v1/resetPassword', {
phone: _self.phone,
password: _self.password
}, (data) => {
this.$vux.toast.text('重置密码成功', 'middle')
_self.$store.commit('SET_LOGIN_ANMATION', '')
_self.$router.replace('/user')
const JIM = this.$store.getters.JIM
if (JIM) {
JIM.updateSelfPwd({
old_pwd: localUser.get('password'),
new_pwd: md5(_self.password),
is_md5: true
}).onSuccess(() => {
localUser.setItem('password', md5(_self.password))
})
}
})
}
}
}



